import React, { Component } from 'react'
import { BrowserRouter } from 'react-router-dom'
import Menubar from './components/Menubar'
import Main from './components/Main'

import './App.css'

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div className="App">
          <Menubar />
          <Main />
        </div>
      </BrowserRouter>
    )
  }
}

export default App;



